import "./topbar.css";
import { Search, Person, Chat, Notifications } from "@material-ui/icons";
import { Link } from "react-router-dom";
import { useContext } from "react";
import { AuthContext } from "../../context/AuthContext";
// import { FaSignOutAlt,faPowerOff } from "react-icons/fa";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPowerOff } from "@fortawesome/free-solid-svg-icons";
//import { useHistory } from "react-router";

export default function Topbar() {
  const { user } = useContext(AuthContext);
  const PF = process.env.REACT_APP_PUBLIC_FOLDER;
  //const history = useHistory();
  const handleLogout = () => {
    localStorage.removeItem("user"); // Remove specific item
    // localStorage.clear(); // Use this to remove all items
    // navigate("/login"); // Redirect to login page
    console.log("user",user._id);
    // history.push("/login");
    window.location.reload();
  };
  return (
    <div className="topbarContainer">
      <div className="topbarLeft">
        <Link to="/" style={{ textDecoration: "none" }}>
          <span className="logo">Anything</span>
        </Link>
      </div>
      <div className="topbarCenter">
        <div className="searchbar">
          <Search className="searchIcon" />
          <input
            placeholder="Search for friend, post or video"
            className="searchInput"
          />
        </div>
      </div>
      <div className="topbarRight">
        <div className="topbarLinks">
          <span className="topbarLink">Homepage</span>
          <span className="topbarLink">Timeline</span>
        </div>
        <div className="topbarIcons">
          <div className="topbarIconItem">
            <Person />
            <span className="topbarIconBadge">1</span>
          </div>
          <Link to="/messenger" >
          <div className="topbarIconItem">
            <Chat />
            <span className="topbarIconBadge">2</span>
          </div>
          </Link>
          <div className="topbarIconItem">
            <Notifications />
            <span className="topbarIconBadge">1</span>
          </div>
        </div>
        <Link to={`/profile/${user.username}`}>
          <img
            src={
              user.profilePicture
                ? PF + user.profilePicture
                : PF + "person/noAvatar.png"
            }
            alt=""
            className="topbarImg"
          />
        </Link>
        <div className="topbarIconItem">
        <button onClick={handleLogout} 
          style={{
          backgroundColor: "#1877f2",
          border: "none",
          borderRadius: "5px",
          padding: "10px",
          cursor: "pointer"
        }}>
          {/* <faPowerOff />  */}
          {/* <i class="fa-solid fa-power-off"></i> */}
          <FontAwesomeIcon icon={faPowerOff} 
            style={{ fontSize:"20px",color: "white", 
              backgroundColor: "#1877f2", border: "none",
              padding:0,margin:0
            }}
          />
        </button>
        </div>
        
      </div>
    </div>
  );
}
